
.login-container {
  position: relative;
  box-sizing: border-box;
  width: 400px;

  margin: 180px auto;
  padding: 35px;

  background-color: #fff;
  border-radius: 15px;
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;

  .login-title {
    text-align: center;
    // margin-bottom: 40px;
    color: #505458;
  }
  .el-form-item {
    display: flex;
  }
  .el-input {
    width: 250px;
  }
  .el-form:nth-child(1) {
    margin-bottom: 20px;
  }

  ::v-deep .el-button {
    margin-left: 50%;
    transform: translateX(-50%);
  }
}
.text {
  font-size: 12px;
  display: flex;
  justify-content: space-between;

  a {
    text-decoration: none;
    color: #0080ff;
  }
  .router-link-active {
    text-decoration: none;
  }
}
